import {createAction} from "redux-actions"
import {
    ADD,
    CHANGE,
    CHANGE_FILTER,
    CLEAR,
    EDIT,
    REMOVE,
    RESET_EDIT_ID,
    RESTORE,
    SET_EDIT_ID,
    TOGGLE
} from "../constants/ActionTypes";

export const change = createAction(CHANGE, (input: string) => input)
export const add = createAction(ADD, (input: string) => ({text: input, done: false}))
export const toggle = createAction(TOGGLE, (id: number) => id)
export const remove = createAction(REMOVE, (id: number) => id)
export const restore = createAction(RESTORE, (data: string) => data)
export const clear = createAction(CLEAR)
export const edit = createAction(EDIT, (id: number, input: string) => ({id, input}))
export const setEditingId = createAction(SET_EDIT_ID, (id: number) => id)
export const resetEditingId = createAction(RESET_EDIT_ID)
export const changeFilter = createAction(CHANGE_FILTER, (filter: string) => filter)
