import {Todo} from "../App"
import {createReducer} from "typesafe-actions"
import {
    ADD,
    CHANGE,
    CHANGE_FILTER,
    CLEAR,
    EDIT,
    REMOVE,
    RESET_EDIT_ID,
    RESTORE,
    SET_EDIT_ID,
    TOGGLE
} from "../constants/ActionTypes";

export interface TodoState {
    input: string,
    todos: Todo[],
    nextTodoId: number,
    filter: string
    editingId:number
}

const initState: TodoState = {
    input: "",
    todos: [],
    nextTodoId: 1,
    filter: "ALL",
    editingId:0
}

const todos = createReducer(
    initState,
    {
        [CHANGE]: (state, {payload: input}) => ({...state, input: input}),
        [ADD]: (state, {payload: todo}) => {
            const newTodo = {...todo, id: state.nextTodoId}
            const nextTodoId = state.nextTodoId + 1
            return {
                ...state,
                todos: state.todos.concat(newTodo),
                nextTodoId
            }
        },
        [TOGGLE]: (state, {payload: id}) => ({
            ...state,
            todos: state.todos.map((todo) => todo.id === id ? {...todo, done: !todo.done} : todo)
        }),
        [REMOVE]: (state, {payload: id}) => ({...state, todos: state.todos.filter((todo) => todo.id != id)}),
        [CLEAR]: (state) => ({...state, todos: []}),
        [RESTORE]: (state, action) => {
            console.log(action)
            console.log(action.todos)
            console.log(action.nextTodoId)
            return {
                ...state,
                todos: action.payload.todos,
                nextTodoId: action.payload.nextTodoId
            }
        },
        [EDIT]: (state, action) => ({...state,
            todos: state.todos.map((todo) => todo.id === action.payload.id ? {
                ...todo,
                text: action.payload.input
            } : todo)
        }),
        [SET_EDIT_ID]: (state, {payload: id}) => ({...state, editingId: id}),
        [RESET_EDIT_ID]: (state) => ({...state, editingId: 0}),
        [CHANGE_FILTER]: (state, {payload: filter}) => ({...state, filter: filter}),
    }
)
export default todos
